import React from "react";

const Button = ({
  style = "",
  onClick = () => {},
  type = "button",
  children,
  text = "",
  ...rest
}) => {
  return (
    <button
      type={type}
      onClick={onClick}
      className={`font-bold text-white ${style}`}
      {...rest}
    >
      <p>{children}</p>
      <p className="text-sm font-normal">{text}</p>
    </button>
  );
};

export default Button;
